import React from 'react';
const Notify = ({ data }) => {
  let weatherNotification = "";
    const weatherIcons = {
        "01d": "Thi tit p  i chi",
        "02d": "Thi tit p  i chi",
        "03d": "Tri p  i lm",
        "04d": "Bn nn mang theo  phng ma",
        "09d": "Bn nn mang theo ",
        "10d": "Bn nn mang theo ",
        "11d": "Tri xu bn nn  nh hoc khng ra ng lc ma to",
        "13d": "Tri lnh gi bn nn mc o m",
        "50d": "Bn nn mang n pin hoc o ma",
        "01n": "Thi tit p  i chi",
        "02n": "Thi tit p  i chi",
        "03n": "Tri p  i lm",
        "04n": "Bn nn mang theo  phng ma",
        "09n": "Bn nn mang theo ",
        "10n": "Bn nn mang theo ",
        "11n": "Tri xu bn nn  nh hoc khng ra ng lc ma to",
        "13n": "Tri lnh gi bn nn mc o m",
        "50n": "Bn nn mang n pin hoc o ma"
    };
    const icon = data.weather[0].icon;
    if (weatherIcons.hasOwnProperty(icon)) {
        weatherNotification = weatherIcons[icon];
    }
    // hasownproperty xc nh xem mt i tng c cha mt thuc tnh c th khng. Phng thc ny tr v mt gi tr boolean
return (<div>{weatherNotification}</div>);
};
export default Notify;